import { DateTime } from "luxon";
import { sleep } from "@/utils/SystemUtils";
import { FormStatus, FormTypes } from "@/constants/FormConstants";

export const formInfoModule = {
  namespaced: true,
  state: {
    name: "",
    savedAt: undefined,
    type: FormTypes.Tech,
    status: FormStatus.Draft,
  },
  getters: {
    getStatus(state) {
      return state.status;
    },
    getSavedAt(state) {
      return state.savedAt;
    },
    getName(state) {
      return state.name;
    },
  },
  mutations: {
    setFormName(state, name) {
      state.name = name;
    },
    setFormStatus(state, status) {
      state.status = status;
    },
    changeFormType(state, type) {
      state.type = type;
    },
    changeSavedAt(state, value) {
      state.savedAt = value;
    },
  },
  actions: {
    async saveForms(store) {
      // Имитация запроса на сервер
      await sleep();

      store.commit("changeSavedAt", DateTime.now().toISO());
    },
  },
};
