import { sleep } from "@/utils/SystemUtils";
import { HttpClient } from "@/helpers/HttpClient";
import { getFormStatus } from "@/helpers/FormsHelpers";

export const authorsFormModule = {
  namespaced: true,
  state: {
    authors: [],
    otherAuthors: [],
    instituteAuthors: [],
    withOtherAuthors: false,
  },
  getters: {
    getAuthors(state) {
      return state.authors;
    },
    getInstituteAuthors(state) {
      return state.instituteAuthors;
    },
    getOtherAuthors(state) {
      return state.otherAuthors;
    },
    getFormStatus(state) {
      const hasOtherAuthors =
        (state.withOtherAuthors && state.otherAuthors.length > 0) ||
        !state.withOtherAuthors;
      const hasInstituteAuthors = state.instituteAuthors.length > 0;

      const authors = [
        ...state.instituteAuthors,
        ...(state.withOtherAuthors ? state.otherAuthors : []),
      ];

      const contribution = authors.reduce(
        (acc, item) => acc + item.creative,
        0
      );

      const isContributionComplete = contribution === 100;

      console.log(">>>>>>", hasOtherAuthors , hasInstituteAuthors , isContributionComplete)

      const isFilled =
        hasOtherAuthors && hasInstituteAuthors && isContributionComplete;
      const isNotFilled =
        !hasOtherAuthors && !hasInstituteAuthors && !isContributionComplete;

      return getFormStatus({ isFilled, isNotFilled });
    },
  },
  mutations: {
    updateCreative(state, { id, value }) {
      const otherAuthorsIndex = state.otherAuthors.findIndex(
        (x) => x.id === id
      );
      const instituteAuthorsIndex = state.instituteAuthors.findIndex(
        (x) => x.id === id
      );

      if (otherAuthorsIndex >= 0) {
        const list = state.otherAuthors.slice();

        list.splice(otherAuthorsIndex, 1, {
          ...state.otherAuthors[otherAuthorsIndex],
          creative: value ? Number(value) : 0,
        });

        state.otherAuthors = list;
      } else if (instituteAuthorsIndex >= 0) {
        const list = state.instituteAuthors.slice();

        list.splice(instituteAuthorsIndex, 1, {
          ...state.instituteAuthors[instituteAuthorsIndex],
          creative: value ? Number(value) : 0,
        });

        state.instituteAuthors = list;
      }
    },
    addInstituteAuthor(state, author) {
      const list = state.instituteAuthors.slice();

      list.push(author);

      state.instituteAuthors = list;
    },
    removeInstituteAuthor(state, index) {
      const list = state.instituteAuthors.slice();

      list.splice(index, 1);

      state.instituteAuthors = list;
    },
    addOtherAuthor(state, author) {
      const list = state.otherAuthors.slice();

      list.push(author);

      state.otherAuthors = list;
    },
    removeOtherAuthor(state, index) {
      const list = state.otherAuthors.slice();

      list.splice(index, 1);

      state.otherAuthors = list;
    },
    changeWithOtherAuthors(state, value) {
      state.withOtherAuthors = value;
    },
    updateInstituteAuthor(state, { index, author }) {
      const list = state.instituteAuthors.slice();

      list.splice(index, 1, author);

      state.instituteAuthors = list;
    },
    updateOtherAuthor(state, { index, author }) {
      const list = state.otherAuthors.slice();

      list.splice(index, 1, author);

      state.otherAuthors = list;
    },
    addAuthors(state, list) {
      state.authors = list;
    },
  },
  actions: {
    async getAuthors(store) {
      const httpClient = new HttpClient({});

      try {
        const list = await httpClient.get("/mock/authors.json");

        // Имитация запроса на сервер
        await sleep(2000);

        store.commit("addAuthors", list);
      } catch (e) {
        return e;
      }
    },
  },
};
