import { getFormStatus } from "@/helpers/FormsHelpers";

export const generalFormModule = {
  namespaced: true,
  state: {
    information: {
      russian: "",
      english: "",
    },
  },
  getters: {
    getRussianInformation(state) {
      return state.information.russian;
    },
    getEnglishInformation(state) {
      return state.information.english;
    },
    getFormStatus(state) {
      const hasRussian = !!state.information.russian;
      const hasEnglish = !!state.information.english;

      const isFilled = hasRussian && hasEnglish;
      const isNotFilled = !hasRussian && !hasEnglish;

      return getFormStatus({ isFilled, isNotFilled });
    },
  },
  mutations: {
    changeRussianInformation(state, value) {
      state.information.russian = value;
    },
    changeEnglishInformation(state, value) {
      state.information.english = value;
    },
  },
};
