import { DateTime } from "luxon";
import {
  FormsTabStatus,
  FormStatus,
  FormTypes,
} from "@/constants/FormConstants";

export function createDraftFormsItem() {
  return {
    name: "Draft",
    type: FormTypes.Tech,
    status: FormStatus.Draft,
    createdAt: DateTime.now().toISO(),
    forms: {
      general: {
        russianInformation: "",
        englishInformation: "",
      },
      authors: {
        instituteAuthors: [],
        otherAuthors: {
          attached: true,
          list: [],
        },
      },
      resources: {
        external: {
          applied: true,
          list: [],
        },
        internal: {
          applied: true,
          russianInformation: "",
          englishInformation: "",
        },
      },
      keyDates: {
        developmentDate: null,
        implementationDate: null,
      },
      disclosure: {
        publishedEarlier: {
          date: null,
          russianInformation: "",
          englishInformation: "",
        },
        planedPublishing: {
          date: null,
          russianInformation: "",
          englishInformation: "",
        },
      },
      organizations: {
        list: [],
      },
      technology: {
        files: [],
      },
      regulation: {
        authorsTakePart: true,
        assignmentNoRussian: "",
        assignmentNoEnglish: "",
        date: null,
      },
    },
  };
}

export function getFormStatus({ isFilled, isNotFilled }) {
  if (isFilled) {
    return FormsTabStatus.Filled;
  }

  if (isNotFilled) {
    return FormsTabStatus.NotFilled;
  }

  return FormsTabStatus.NotFullyFilled;
}
